<template>
  <div id="app">
    <nav class="navbar" role="navigation" aria-label="main navigation">
      <div class="container">
        <div class="navbar-brand">
          <div class="navbar-start">
            <a class="navbar-item">My Company Logo</a>
          </div>

          <a
            role="button"
            class="navbar-burger burger"
            aria-label="menu"
            aria-expanded="false"
            v-on:click="toggleNav"
            :class="{'is-active': isActive}"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div class="navbar-menu" v-on:click="toggleNav" :class="{'is-active': isActive}">
          <div class="navbar-end">
            <router-link to="/" class="navbar-item">Home</router-link>
            <router-link to="faq" class="navbar-item">Features</router-link>
            <router-link to="faq" class="navbar-item">About</router-link>
            <router-link to="faq" class="navbar-item">FAQ</router-link>

            <div class="navbar-item">
              <div class="buttons">
                <a class="button is-primary is-outlined">
                  <span class="icon">
                    <i class="fa fa-download"></i>
                  </span>
                  <span>Join Now</span>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </nav>
    <router-view></router-view>

    <footer class="footer is-primary">
      <div class="container">
        <div class="columns">
          <div class="column">
            <p>This is the footer, where you can put stuff.</p>
          </div>
          <div class="column has-text-right">
            <a class="icon" href="#">
              <i class="fa fa-facebook"></i>
            </a>
            <a class="icon" href="#">
              <i class="fa fa-twitter"></i>
            </a>
          </div>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      isActive: false
    };
  },
  methods: {
    toggleNav: function() {
      this.isActive = !this.isActive;
    }
  }
};
</script>

<style lang='sass' scoped>
@import '../node_modules/bulma/bulma.sass'
@import 'mq'

body 
  margin: 0

.navbar
  background-color: #383838
  a:hover
    color: gray

.navbar-start a
  color: #fff
  font-weight: bold

a.navbar-item
  color: #C1C1C1
  padding: 0.5rem 1.75rem

.navbar-burger
  span
    background-color: #C1C1C1
  +mobile
    color: gray
    &:hover
      background-color: #F1F1F1
  
.footer
  background-color: $primary !important
  color: #fff

  .icon
    color: #fff
    margin-left: 20px
</style>



